/* Week5.css */
body{
    background-color: rgba(225, 114, 11, 0.463);
}

.week5-container {
    
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}
  
  .card-container {
    
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
  }
  
  .card {
    border: 5px solid #0955e3;
    border-radius: 15px;
    overflow: hidden;
    background-color: #05fe757f;
    
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  
  .card:hover {
    background-color: rgba(13, 210, 232, 0.998);
    box-shadow:  8px 15px rgba(0, 0, 0, 0.856);
    transform: translateY(-3px);
  }
  
  .card-body {
    padding: 15px;
  }
  
  .card-title {

    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .card-text {
    font-size: 14px;
    margin-bottom: 5px;
  }

  h5{
    color: rgba(238, 7, 22, 0.877);
  }
